<template>
  <div class="hs-container">
    <c-header :title="$t('base.Rescheduled')" isBackNav></c-header>
    <hs-scroll
      class="table-view"
      ref="tableView"
      :data="[details]"
      :options="tableViewOptions"
      @pulling-down="_getData">
      <ul>
        <div class="warning">{{$t('base.ReminderPassengersWhoHaveCheckedInMustFirstCancelTheCheckIn')}}</div>
        <hs-section v-if="details.Segments && !noValue(JSON.stringify(details.Segments))">
          <div v-for="(item, index) in details.Segments" :key="index" style="width: 100%" @click="_toggleSegmentSelect(index)">
            <!-- <hs-cell-1 :showLogo="false">
              <div class="select-travel">
                <div class="logo" v-if="item.isSelected" style="background-image: url('static/image/base/check-box/check-box.png')"></div>
                <div class="logo" v-else style="background-image: url('static/image/base/check-box/check-box-default.png')"></div>
                <p>选择</p>
              </div>
            </hs-cell-1> -->
            <hs-cell-1 :showLogo="false">
              <div class="travel-details">
                <div class="left">
                  <div class="time">
                    <div class="mark">{{_showSegmentTitle(index)}}</div>
                    <p>{{beTime(item.DepartDate, 'MM月dd日')}} {{getWeeks(beTime(item.DepartDate))}} {{item.DepartCityName}}&nbsp;-&nbsp;{{item.ArrivalCityName}}</p>
                  </div>
                  <div class="plane">
                    <div class="logo" :style="{'background-image': `url(${item.AirLineLogoUrl})`}"></div>
                    <p>{{item.AirLineName}}<template>&nbsp;<span v-if="!noValue(item.ActualCarryFlightNo)">{{$t('base.CodeShare')}}</span></template>&nbsp;{{item.FlightNo}}<template v-if="!noValue(item.Plane)">&nbsp;|&nbsp;{{item.Plane}}</template><template v-if="!noValue(item.Meal)">&nbsp;|&nbsp;{{item.Meal}}</template>
                    </p>
                  </div>
                  <div class="plane">
                    <p v-if="!noValue(item.ActualCarryFlightNo)">{{$t('base.OperatedBy')}}&nbsp;{{item.ActualCarryFlightNo}}</p>
                  </div>
                  <div class="details">
                    <div class="source">
                      <p>{{beTime(item.DepartDate, 'hh:mm')}}</p>
                      <p>{{item.DepartAirportName}} {{item.DepartTerminal}}</p>
                    </div>
                    <div class="to">
                      <div class="stopOver-logo" v-if="item.StopOver">
                        <p>{{$t('base.By')}}</p>
                      </div>
                      <div class="logo" v-else></div>
                      <div class="dashed"></div>
                      <p class="stopOver" v-if="item.StopOver">{{item.StopOver.StopCity}}</p>
                    </div>
                    <div class="destination">
                      <p>{{beTime(item.ArrivalDate, 'hh:mm')}}<span v-if="item.AddDays > 0">+{{item.AddDays}}</span></p>
                      <p>{{item.ArrivalAirportName}} {{item.ArrivalTerminal}}</p>
                    </div>
                  </div>
                </div>
                <div class="right">
                  <div class="logo" v-if="item.isSelected" style="background-image: url('static/image/base/check-box/check-box.png')"></div>
                  <div class="logo" v-else style="background-image: url('static/image/base/check-box/check-box-default.png')"></div>
                </div>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" @click.native.stop="_getFlightCabinRule(item.Rule)">
              <div class="cost-details">
                <div class="details">
                  <div><span>{{$t('base.AirTickets')}}</span><span>￥{{item.SalePrice ? item.SalePrice : 0}}</span></div>
                  <div>
                    <span>{{$t('base.PlaneConstruction')}}&nbsp;/&nbsp;{{$t('base.Fuel')}}</span>
                    <span>
                      ￥{{
                        (item.FuelOilTax ? item.FuelOilTax : 0) +
                        (item.AirportTax ? item.AirportTax : 0)
                      }}
                    </span>
                  </div>
                </div>
                <p>{{$t('base.TicketRestrictions')}} ></p>
              </div>
            </hs-cell-1>
          </div>
        </hs-section>
        <hs-section v-if="selectedSegments.length > 0">
          <hs-cell1 @click.native="showWhenGo = true" v-if="selectedSegments[0]">
            <div class="travel-date">
              <p>{{$t('base.DepartureDate')}}&nbsp;&nbsp;{{flightOrder.GoSelectedFlight.DepartDate}}&nbsp;<span>{{getWeeks(flightOrder.GoSelectedFlight.DepartDate)}}</span></p>
            </div>
          </hs-cell1>
          <hs-cell1 @click.native="showWhenBack = true" v-if="selectedSegments[1]">
            <div class="travel-date">
              <p>{{$t('base.ArrivalDate')}}&nbsp;&nbsp;{{flightOrder.BackSelectedFlight.DepartDate}}&nbsp;<span>{{getWeeks(flightOrder.BackSelectedFlight.DepartDate)}}</span></p>
            </div>
          </hs-cell1>
        </hs-section>
      </ul>
    </hs-scroll>
    <div class="footer">
      <hs-button width="50%" height="40px" @click="_gotoSearch">{{$t('base.Search')}}</hs-button>
    </div>
    <c-return-explain v-model="showExplain" :details="returnExplainDetails"></c-return-explain>
    <hs-date-picker v-model="showWhenGo" :date="flightOrder.GoSelectedFlight.DepartDate" @changeDate="_setDateWhenGo" :title="$t('base.DepartureDate')"></hs-date-picker>
    <hs-date-picker v-model="showWhenBack" :date="flightOrder.BackSelectedFlight.DepartDate" @changeDate="_setDateWhenBack" :overdueDate="flightOrder.GoSelectedFlight.DepartDate" :title="$t('base.ReturnDate')"></hs-date-picker>
  </div>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsButton from 'base/hs-button/hs-button'
import CReturnExplain from 'components/c-return-explain/c-return-explain'
import HsDatePicker from 'base/hs-date-picker/hs-date-picker'
import {kpiFlightGetChangeSelectInitInfo} from 'api'
import {mapGetters, mapMutations} from 'vuex'
export default {
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsCell1,
    HsButton,
    CReturnExplain,
    HsDatePicker
  },
  created() {
    this._getData()
  },
  watch: {
    'flightOrder.GoSelectedFlight.DepartDate': {
      handler: function(val) {
        if (val > this.flightOrder.BackSelectedFlight.DepartDate) {
          let res = JSON.parse(JSON.stringify(this.flightOrder))
          res.BackSelectedFlight.DepartDate = this.flightOrder.GoSelectedFlight.DepartDate
          res.BackLowestPriceFlights.DepartDate = this.flightOrder.GoSelectedFlight.DepartDate
          this.SET_FLIGHT_ORDER(res)
        }
      },
      deep: true
    }
  },
  computed: {
    ...mapGetters([
      'flightOrder'
    ])
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      details: {},
      showExplain: false,
      returnExplainDetails: {},
      showWhenGo: false,
      showWhenBack: false,
      whereGo: '',
      whereBack: '',
      selectedSegments: []
    }
  },
  methods: {
    ...mapMutations([
      'SET_FLIGHT_ORDER'
    ]),
    _getData() {
      let params = {
        ItktID: this.$route.query.ItktID,
        PnrID: this.$route.query.PnrID
      }
      kpiFlightGetChangeSelectInitInfo(params).then((res) => {
        console.log('改期初始化： ', res)
        this.details = res.ResultData
        this.details.Segments.forEach((element, index) => {
          this.$set(this.details.Segments[index], 'isSelected', false)
        })
        this.details.Segments.length === 1 && this._toggleSegmentSelect(0)
      })
    },
    // 根据航段显示单程/往返程/N程
    _showSegmentTitle(index) {
      let length = this.details.Segments && !this.noValue(JSON.stringify(this.details.Segments)) ? this.details.Segments.length : 0
      if (length === 1) {
        return this.$t('base.OneWay')
      } else if (length === 2) {
        if (this.details.Segments[0].DepartCityName === this.details.Segments[1].ArrivalCityName) {
          return index === 0 ? this.$t('base.Going') : this.$t('base.Return')
        } else {
          return this.$t('base.NoXSegment', 'function', index + 1)
        }
      } else {
        return this.$t('base.NoXSegment', 'function', index + 1)
      }
    },
    // 退改签信息
    _getFlightCabinRule(rule) {
      this.showExplain = true
      this.returnExplainDetails = {
        vhtml: rule
      }
    },
    // 选择航段
    _toggleSegmentSelect(index) {
      this.details.Segments[index].isSelected = !this.details.Segments[index].isSelected
      this.selectedSegments = []
      this.details.Segments.forEach((element, index) => {
        if (element.isSelected) {
          this.selectedSegments.push(element)
        }
      })
    },
    _setDateWhenGo(val) {
      let res = JSON.parse(JSON.stringify(this.flightOrder))
      res.GoSelectedFlight.DepartDate = val
      this.SET_FLIGHT_ORDER(res)
    },
    _setDateWhenBack(val) {
      let res = JSON.parse(JSON.stringify(this.flightOrder))
      res.BackSelectedFlight.DepartDate = val
      this.SET_FLIGHT_ORDER(res)
    },
    _gotoSearch() {
      if (this.selectedSegments.length === 0) {
        this.$toast(this.$t('TM.PleaseSelectTheItineraryToBeRescheduled'))
      } else {
        this.whereGo = this.selectedSegments[0].DepartCityName
        this.whereBack = this.selectedSegments[0].ArrivalCityName
        let res = JSON.parse(JSON.stringify(this.flightOrder))
        res.OriginBookingID = this.$route.query.ItktID
        res.OriginPnrID = this.$route.query.PnrID
        res.TravelType = this.$route.query.TravelType
        res.DepartCode = this.selectedSegments[0].DepartCityCode
        res.ArrivalCode = this.selectedSegments[0].ArrivalCityCode
        res.DepartCityCode = this.selectedSegments[0].DepartCityCode
        res.ArrivalCityCode = this.selectedSegments[0].ArrivalCityCode
        res.DepartCityName = this.selectedSegments[0].DepartCityName
        res.ArrivalCityName = this.selectedSegments[0].ArrivalCityName
        res.SegmentIDList = this.selectedSegments.map(v => v.ID)
        this.SET_FLIGHT_ORDER(res)
        this.$router.push({path: '/planeTicket/inforList', query: {FlightClass: 0, isOneWay: this.selectedSegments.length === 1}})
      }
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  >.table-view
    width 100%
    height calc(100% - 45px - 50px)
    overflow hidden

    // 提示
    .warning
      @extend .flex-row
      @extend .flex-start
      background-color $color_yellow_background_warning
      color $color_orange_text_warning
      font-size 0.8rem
      line-height 1.2em
      width 100%
      padding 8px 10px
      text-align justify

    .select-travel
      @extend .flex-row
      @extend .flex-end
      width 100%
      >p
        font-size 1rem
        color $color_gray_text
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        margin-right 10px
    .travel-details
      @extend .flex-row
      @extend .flex-between
      width 100%
      >.right
        @extend .flex-center
        width 20px
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
      >.left
        width calc(100% - 35px)
        padding-top 15px
        >.time
          @extend .flex-row
          @extend .flex-start
          width 100%
          margin-bottom 5px
          >.mark
            @extend .flex-center
            font-size 0.7rem
            border-radius 2px
            border 1px solid $color_theme
            color $color_theme
            padding 3px 0
            width 40px
            margin-right 10px
          >p
            width calc(100% - 50px)
            line-height 1.3em
        >.plane
          @extend .flex-row
          @extend .flex-start
          width 100%
          font-size 0.8rem
          color $color_gray_text
          line-height 1.6em
          >.logo
            width 15px
            height 15px
            background-size 100% 100%
            margin-right 3px
          >p
            >span
              color $color_theme
        >.details
          @extend .flex-row
          @extend .flex-between
          width 100%
          padding 15px 0
          >.source
          >.destination
            @extend .flex-column
            @extend .flex-center
            width 100px
            >p
              text-align center
              &:nth-child(1)
                font-size 1.2rem
                font-weight bold
                position relative
                line-height 1.3em
                >span
                  color $color_theme
                  font-size 0.8rem
                  position absolute
                  top -4px
                  right -16px
              &:nth-child(2)
                font-size 0.8rem
                line-height 1.3em
          >.to
            @extend .flex-center
            width calc(100% - 200px)
            position relative
            >.logo
              width 15px
              height 15px
              background-size 100% 100%
              background-image url('../../../../../static/image/home/trip_plane.png')
              background-color $color_white
            >.dashed
              width 100%
              height 0px
              position absolute
              left 0
              top 50%
              border-top 1px dashed $color_black_text_0
            >.stopOver
              position absolute
              top 18px
              left 0
              font-size 0.6rem
              color $color_theme
              width 100%
              text-align center
            >.stopOver-logo
              @extend .flex-center
              width 24px
              height 18px
              background-color $color_white
              z-index 9
              >p
                text-align center
                width 18px
                line-height 1.2em
                color $color_theme
                font-size 0.6rem
    .cost-details
      @extend .flex-row
      @extend .flex-between
      width 100%
      padding 15px 0
      font-size 0.8rem
      .details
        @extend .flex-column
        @extend .flex-top
        width calc(100% - 60px)
        >div
          margin 2px 0
          >span
            &:nth-child(1)
              margin-right 6px
            &:nth-child(2)
              color $color_theme
      >p
        color $color_gray_text
    .travel-date
      @extend .flex-row
      @extend .flex-start
      font-size 1rem
      color $color_black_text_0
      >p
        >span
          font-size 0.8rem
          color $color_gray_text
  >.footer
    @extend .flex-row
    @extend .flex-center
    width 100%
    height 50px
    background-color $color_white
    border-top 1px solid $color_gray_layer
    padding 0 15px
</style>



